html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.3;
  font-family: "Barlow", sans-serif;
  /*  font-family: "Barlow Condensed", sans-serif; */
  font-size: 1.125rem;
  color: #a0a1ac;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.header {
  padding: 0 1.875rem;
}
.header nav {
  padding-top: 35px;
  padding-bottom: 60px;
  padding-left: 29px;
  padding-right: 29px;
}
.header__menu > span {
  display: block;
  width: 26px;
  height: 2px;
  background-color: #5b5f71;
}
.header__menu > span:not(:last-child) {
  margin-bottom: 3px;
}
.header__links a {
  text-decoration: none;
  color: #242942;
  text-transform: uppercase;
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 700;
  font-size: 0.875rem;
}
.header__links a:not(:last-child) {
  margin-right: 2.5rem;
}
.header__links :hover {
  text-decoration: underline;
  color: #a0a1ac;
}
.header__links :active {
  color: #a0a1ac;
}